.contact{
    margin: 80px auto;
    max-width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.contact-col{
    flex-basis: 48%;
    color: #676767;
}

.contact-col h3{
    color: #000f38;
    font-weight: 500;
    font-size: 25px;
}

.contact-col p{
    max-width: 450px;
    line-height: 2;
}

.contact-col ul li{
    display: flex;
    align-items: center;
    margin: 20px 0;
}

.contact-col ul li img{
    width: 30px;
    margin-right: 10px;
}

.contact form input ,  .contact form textarea{
    display: block;
    width: 100%;
    background-color: #7ED4AD;
    padding: 15px;
    border: 0;
    outline: 0;
    margin-top: 5px;
    margin-bottom: 15px;
    resize: none;
}

.contact form span{
    display: block;
    margin: 20px 0;
}

@media(max-width:700px){
    .contact{
        display: block;
    }
    .contact-col{
        padding: 20px;
    }
}
