nav{
    width: 100%;
    color: #fff;
    padding: 4px 0;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 10;
}

nav .logo{
    width: 180px;
}

nav ul li{
    display: inline-block;
    list-style: none;
    margin: 5px 20px;
    font-size: 16px;
    cursor: pointer;
}

.dark-nav{
    background-color: #31511E;
    transition: .5s;
}

.menu{
    display: none;
}
/*                media query            */
@media(max-width:1000px){
    .logo{
        width: 14px;

    }

    nav ul li{
        margin: 10px 15px;
    }
}

@media(max-width:840px){
    nav{
        padding: 15px 0;
    }

    nav ul{
        position: fixed;
        top: 0;
        right: -200px;
        bottom: 0;
        background-color: #212Ea0;
        z-index: -1;
        width: 200px;
        padding-top: 70px;
        transition: 0.5s;
    }
    nav ul li{
        display: block;
        margin: 25px 40px;
    }
    
    .menu{
        display: block;
        width: 30px;
        cursor: pointer;
    }

    .hide-mobile-menu{
        right: 0;
    }
}
